<template>
      <el-container>
      <el-header>
        <el-form :inline="true" :model="formInline" class="demo-form-inline">
            <el-form-item label="审批人">
              <el-input v-model="formInline.user" placeholder="审批人"></el-input>
            </el-form-item>
            <el-form-item label="活动区域">
              <el-select v-model="formInline.region" placeholder="活动区域">
                <el-option label="区域一" value="shanghai"></el-option>
                <el-option label="区域二" value="beijing"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="onSubmit">查询</el-button>
          </el-form-item>
        </el-form>
      </el-header>
    <el-main>
      <template>
        <el-table
          :data="tableData"
          style="width: 100%"
          v-loading="loading"
          >
          <el-table-column
            prop="year"
            label="年份"
            width="180">
          </el-table-column>
          <el-table-column
            prop="month"
            label="月份"
            width="180">
          </el-table-column>
          <el-table-column
            prop="date"
            label="日期">
          </el-table-column>
          <el-table-column
            prop="yearweek"
            label="年份周数">
          </el-table-column>
          <el-table-column
            prop="yearday"
            label="年份天数">
          </el-table-column>
        </el-table>
      </template>
      <template>
        <div class="block">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage4"
            :page-sizes="[100, 200, 300, 400]"
            :page-size="100"
            layout="total, sizes, prev, pager, next, jumper"
            :total="400">
          </el-pagination>
        </div>
      </template>
    </el-main>
    </el-container>
</template>
<script>
export default {
  data: function () {
    return {
      tableData: [],
      formInline: {
        user: '',
        region: ''
      },
      isCollapse: false,
      currentPage4: 4,
      loading: false
    }
  },
  methods: {
    onSubmit () {
      this.loading = true
      console.log(this.formInline.user)
      this.$http.get('/holiday/get?cn=1').then((response) => {
        this.tableData = response.data.data.list
        this.loading = false
      }).catch(function (error) {
        console.log(error)
      })
    },
    handleSizeChange (val) {
      console.log(`每页 ${val} 条`)
    },
    handleCurrentChange (val) {
      console.log(`当前页: ${val}`)
    }
  }
}
</script>
<style lang="less" scoped>
.el-header, .el-footer {
    background-color: #fff;
    color: #333;
    text-align: left;
  }

  .el-aside {
    background-color: #fff;
    color: #333;
    text-align: center;
  }
  .el-main {
    background-color: #f5f7f9;
    color: #333;
    text-align: center;
    padding: 0px;
  }
  body > .el-container {
    margin-bottom: 40px;
  }
  .el-container:nth-child(5) .el-aside,
  .el-container:nth-child(6) .el-aside {
    line-height: 260px;
  }
  .el-container:nth-child(7) .el-aside {
    line-height: 320px;
  }
  .demo-form-inline {
  margin-top: 10px;
  }

  .el-menu-vertical-demo:not(.el-menu--collapse) {
    width: 240;
    min-height: 400px;
  }
  .block{
    text-align: right;
  }
</style>
